import React, { useEffect, useRef } from "react";
import classes from "./about.module.css";
import calc from "../../assets/calc.png";
import weat from "../../assets/weat.png";
import typ from "../../assets/typing.jpeg";

let img_div;
let text_div;
let atP = 0;

export default function About() {
  let ref1 = useRef(null);
  let ref2 = useRef(null);
  let ref3 = useRef(null);
  let ref4 = useRef(null);
  let ref5 = useRef(null);

  useEffect(() => {
    img_div = ref4.current;
    text_div = ref5.current;
  }, []);
  function replace(position) {
    if (window.innerWidth > 800) {
      if (position === "down") {
        atP++;
        if (atP === 3) atP = 0;
        img_div.style.transform = `translateY(-${atP}00%)`;
        text_div.style.transform = `translateY(${atP - 2}00%)`;
      }
      if (position === "up") {
        atP++;
        if (atP === 3) atP = 0;
        img_div.style.transform = `translateY(${atP - 2}00%)`;
        text_div.style.transform = `translateY(-${atP}00%)`;
      }
    } else {
      if (position === "right") {
        atP++;
        if (atP === 3 || atP < 0) atP = 0;
        img_div.style.transform = `translateX(-${atP * 30}rem)`;
        text_div.style.transform = `translateX(${atP * 30 - 60}rem)`;
      }
      if (position === "left") {
        atP--;
        if (atP === 3 || atP < 0) atP = 2;
        img_div.style.transform = `translateX(-${atP * 30}rem)`;
        text_div.style.transform = `translateX(${atP * 30 - 60}rem)`;
      }
    }
  }
  return (
    <section className={classes.proj_sec}>
      <div className={classes.title}>PROJECT</div>
      <div ref={ref1} className={classes.container}>
        <button
          onClick={() => replace("up")}
          ref={ref2}
          className={classes.up_but}
        >
          &darr;
        </button>

        <button
          onClick={() => replace("down")}
          ref={ref3}
          className={classes.down_but}
        >
          &uarr;
        </button>
        <button
          onClick={() => replace("left")}
          ref={ref3}
          className={classes.left_but}
        >
          &larr;
        </button>
        <button
          onClick={() => replace("right")}
          ref={ref3}
          className={classes.right_but}
        >
          &rarr;
        </button>
        <div ref={ref5} className={classes.text_slide}>
          <div className={classes.proj_info}>
            <div className={classes.proj_title}>Scientific Calculator</div>
            <div>
              Lorem ipsum dolor sit amet, consectetur adipisicing elit. Tenetur,
              tempore ratione. Ipsam, inventore a natus dignissimos placeat aut,
              quis quae, soluta est modi debitis ducimus.
            </div>
            <div className={classes.tools}>
              <span>Python</span>
              <span>Tkinter</span>
            </div>
          </div>
          <div className={classes.proj_info}>
            <div className={classes.proj_title}>Weather Report</div>
            <div>
              Lorem ipsum dolor sit amet, consectetur adipisicing elit. Tenetur,
              tempore ratione. Ipsam, inventore a natus dignissimos placeat aut,
              quis quae, soluta est modi debitis ducimus.
            </div>
            <div className={classes.tools}>
              <span>HTML</span>
              <span>CSS</span>
              <span>Javascript</span>
              <span>NodeJs</span>
            </div>
          </div>
          <div className={classes.proj_info}>
            <div className={classes.proj_title}>Typing Championship</div>
            <div>
              Lorem ipsum dolor sit amet, consectetur adipisicing elit. Tenetur,
              tempore ratione. Ipsam, inventore a natus dignissimos placeat aut,
              quis quae, soluta est modi debitis ducimus.
            </div>
            <div className={classes.tools}>
              <span>NodeJs</span>
              <span>Socket.io</span>
              <span>Javascript</span>
            </div>
          </div>
        </div>
        <div ref={ref4} className={classes.proj_img_cont}>
          <div>
            <img className={classes.proj_img} src={typ} alt="img" />
          </div>
          <div>
            <img className={classes.proj_img} src={weat} alt="img" />
          </div>
          <div>
            <img className={classes.proj_img} src={calc} alt="img" />
          </div>
        </div>
      </div>
    </section>
    // </div>
  );
}
